/*
============================================================================= */

@import url('https://p.typekit.net/p.css?s=1&k=jyw7rzz&ht=tk&f=51610.51611&a=119462845&app=typekit&e=css');

/**
 * This injects Tailwind's base styles and any base styles registered by
 * plugins.
 */
@tailwind base;
/**
 * This injects Tailwind's component classes and any component classes
 * registered by plugins.
 */
@tailwind components;
/**
 * This injects Tailwind's utility classes and any utility classes registered
 * by plugins.
 */
@tailwind utilities;

/*
============================================================================= */

@layer base {
	/* add base styles here */
	/* @import url("https://use.typekit.net/jyw7rzz.css"); */
	/* font-family: "rotunda-variable", sans-serif; */
	/* font-variation-settings: "wght" 400; */

	@font-face {
		ascent-override: 101%;
		descent-override: normal;
		font-display: auto;
		font-family: 'rotunda-variable';
		font-stretch: normal;
		font-style: normal;
		/* font-weight: 400; */
		font-weight: 100 900;
		/* https://stackoverflow.com/questions/28101813/fix-custom-font-line-height-with-css */
		line-gap-override: normal;
		src: url('https://use.typekit.net/af/ccc4ad/0000000000000000774b9a96/30/l?primer=7cdcb44be4a7db8877ffa5c0007b8dd865b3bbc383831fe2ea177f62257a9191&fvd=n4&v=3')
				format('woff2'),
			url('https://use.typekit.net/af/ccc4ad/0000000000000000774b9a96/30/d?primer=7cdcb44be4a7db8877ffa5c0007b8dd865b3bbc383831fe2ea177f62257a9191&fvd=n4&v=3')
				format('woff'),
			url('https://use.typekit.net/af/ccc4ad/0000000000000000774b9a96/30/a?primer=7cdcb44be4a7db8877ffa5c0007b8dd865b3bbc383831fe2ea177f62257a9191&fvd=n4&v=3')
				format('opentype');
	}

	@font-face {
		ascent-override: 101%;
		descent-override: normal;
		font-display: auto;
		font-family: 'rotunda-variable';
		font-stretch: normal;
		font-style: italic;
		/* font-weight: 400; */
		font-weight: 100 900;
		/* https://stackoverflow.com/questions/28101813/fix-custom-font-line-height-with-css */
		line-gap-override: normal;
		src: url('https://use.typekit.net/af/90c4a3/0000000000000000774b9a97/30/l?primer=7cdcb44be4a7db8877ffa5c0007b8dd865b3bbc383831fe2ea177f62257a9191&fvd=i4&v=3')
				format('woff2'),
			url('https://use.typekit.net/af/90c4a3/0000000000000000774b9a97/30/d?primer=7cdcb44be4a7db8877ffa5c0007b8dd865b3bbc383831fe2ea177f62257a9191&fvd=i4&v=3')
				format('woff'),
			url('https://use.typekit.net/af/90c4a3/0000000000000000774b9a97/30/a?primer=7cdcb44be4a7db8877ffa5c0007b8dd865b3bbc383831fe2ea177f62257a9191&fvd=i4&v=3')
				format('opentype');
	}

	.tk-rotunda-variable {
		font-family: 'rotunda-variable', sans-serif;
	}

	body {
		font-family: 'rotunda-variable', sans-serif;
	}
}

@layer components {
	/* add component styles here */
	/*	banner
	============================================================================= */
	.banner {
		@apply mx-4 mb-2 mt-4 h-[160px] overflow-hidden rounded-xl bg-gradient-to-r from-purple-700 from-10% via-red-600  via-60% to-orange-600 shadow-lg max_md:h-auto max_md:py-8;
	}

	.banner.banner-purple {
		@apply from-purple-600 from-10% via-purple-700  via-60% to-purple-500 py-8;
	}

	.banner > .banner-container {
		@apply flex h-full items-start justify-between text-white max_md:flex-col;
	}
	.banner > .banner-container > .title {
		@apply flex h-full flex-[2] flex-col items-start justify-center  pl-4;
	}
	.banner > .banner-container > .title > h2 {
		@apply font-bold uppercase leading-tight text-white drop-shadow-lg;
		font-size: calc(2em + 1vw);
		line-height: calc(0.75em + 1vw);
	}
	.banner > .banner-container > .title > p {
		@apply text-right font-light leading-tight text-white drop-shadow-lg;
		font-size: calc(0.33em + 1vw);
		line-height: calc(0.75em + 1vw);
	}

	.banner > .banner-container > .new-items {
		@apply flex h-full flex-[3] flex-col items-start justify-center gap-4 border-l border-white/10 pl-8 text-base font-bold drop-shadow-sm max_md:mt-4 max_md:border-l-0 max_md:border-t max_md:pl-4 max_md:pt-4;
	}

	/*	cards
	============================================================================= */
	.card {
		@apply overflow-hidden rounded-xl shadow-lg;
	}
	.card-hero {
		@apply cursor-pointer transition-transform duration-300 ease-in-out hover:scale-[102%];
	}
	.card-header {
		@apply flex items-center justify-between border-b border-slate-600 bg-gradient-to-b from-slate-300 to-slate-500 px-4 py-3  font-extrabold text-white;
	}
	.card-header > .title {
		@apply text-2xl font-semibold uppercase drop-shadow-[1px_1px_0_rgba(0,0,0,1)];
		font-size: calc(0.5em + 1vw);
		line-height: calc(0.6em + 1vw);
	}
	.card-header > .badge {
		@apply flex h-8 w-8 items-center justify-center rounded-full bg-slate-600 text-lg text-white shadow-inner;
	}

	/* hero cards */
	.card-thumbnail > figure {
		/* @apply h-[240px] overflow-hidden; */
		@apply relative w-full overflow-hidden pt-[50%];
	}

	.card-thumbnail > figure > img {
		@apply absolute left-0 top-0 h-full w-full object-cover;
	}

	.card-title {
		@apply border-t border-slate-500 bg-white p-4;
	}

	.card-title > .title-container {
		@apply -mr-1 flex items-start justify-between gap-4;
	}

	.card-title > .title-container .title {
		@apply flex-1 text-xl font-medium leading-tight text-slate-900;
	}

	.card-title > .title-container .icon > svg {
		@apply h-8 w-8 fill-current stroke-current;
	}

	.card-title > .meta-data {
		@apply mt-2 flex h-4 items-baseline justify-end text-xs text-slate-400;
	}

	/* card-body: standard cards */
	.card-body {
		@apply bg-white pb-6 pt-2;
	}

	/* card-body-item */
	.card-body-item {
		@apply flex cursor-pointer items-center justify-between gap-1 border-b border-gray-200 hover:bg-slate-300;
	}
	.card-body-item:first-child {
		@apply border-t;
	}
	.card-body-item > .icon {
		@apply pl-4;
	}

	.icon > svg {
		@apply h-4 w-4 transform fill-current stroke-current text-slate-500;
	}

	.card-body-item > .title {
		@apply flex-1 px-2 py-3 text-sm font-bold leading-tight text-slate-800;
	}
	.card-body-item > .date {
		@apply px-4 py-2 text-xs font-bold leading-tight text-slate-500;
	}

	.card-body > .meta-data {
		@apply mt-4 flex h-4 items-baseline justify-start px-4 pb-4 text-xs;
	}
	.card-body > .meta-data > button {
		@apply flex items-center justify-center rounded-md bg-white px-2 py-1 text-sm font-bold text-slate-600 hover:bg-slate-200 focus:outline-none active:bg-slate-300;
	}

	/* contact-item */
	.contact-container {
		@apply flex flex-wrap items-center justify-start gap-4 py-4 max_lg:flex-col max_lg:items-start;
	}
	.contact-item {
		@apply flex items-center justify-between gap-2 pr-4;
	}
	.contact-item > .icon {
		@apply pl-2;
	}
	.contact-item > .icon > svg {
		@apply h-24 w-24 fill-current stroke-current text-slate-400;
	}
	.contact-item > .contact-details {
		@apply text-sm;
	}
	.contact-item > .contact-details > h3 {
		@apply text-lg font-bold leading-tight text-slate-800;
	}

	/* card color variations */
	.card-orange {
		.card-header {
			@apply border-red-700 from-orange-300 to-red-500;
			.badge {
				@apply bg-orange-800;
			}
		}
		.card-body-item {
			@apply hover:bg-orange-50;
		}
		.icon > svg {
			@apply text-orange-600;
		}
	}
	.card-blue {
		.card-header {
			@apply border-blue-700 from-blue-300 to-blue-500;
			.badge {
				@apply bg-blue-800;
			}
		}
		.card-body-item {
			@apply hover:bg-blue-50;
		}
		.icon > svg {
			@apply text-blue-600;
		}
	}
	.card-red {
		.card-header {
			@apply border-red-700 from-red-400 to-red-600;
			.badge {
				@apply bg-red-800;
			}
		}
		.card-body-item {
			@apply hover:bg-red-50;
		}
		.icon > svg {
			@apply text-red-600;
		}
	}
	.card-green {
		.card-header {
			@apply border-green-700 from-green-400 to-green-600;
			.badge {
				@apply bg-green-800;
			}
		}
		.card-body-item {
			@apply hover:bg-green-50;
		}
		.icon > svg {
			@apply text-green-600;
		}
	}
	.card-purple {
		.card-header {
			@apply border-purple-700 from-purple-400 to-purple-600;
			.badge {
				@apply bg-purple-800;
			}
		}
		.card-body-item {
			@apply hover:bg-purple-50;
		}
		.icon > svg {
			@apply text-purple-600;
		}
	}

	/*	resources view
	============================================================================= */
	.standard-cards-resources.columns-3 {
		@apply pt-12;
	}
	.standard-cards-resources.columns-3 .card {
		@apply mt-8;
	}
	.standard-cards-resources.columns-3 .card:first-child {
		@apply mt-0;
	}
}

@layer utilities {
	/* add utility styles here */
}

/**
* The screen function allows you to create media queries that reference
* your breakpoints by name instead of duplicating their values in your own CSS.
*/

@media screen(max_md) {
	.card:not(:first-child) {
		@apply mt-4;
	}

	.card-header > .title {
		font-size: 1.25rem;
		line-height: 1.4rem;
	}

	.banner > .banner-container > .title > h2 {
		font-size: 3rem;
		line-height: 3.25rem;
	}
	.banner > .banner-container > .title > p {
		font-size: 1.25rem;
		line-height: 1.4rem;
	}
}
